import {Col, Row, Card} from "reactstrap"
import React, {useEffect, useState} from "react"
import {useParams} from "react-router-dom"
import {useDispatch, useSelector} from "react-redux"
import {getGallery, updateStatusGallery} from "../store/action"
import './styles/gallery_detail.scss'
import CreatableSelect from "react-select/creatable/dist/react-select.esm"
import CommonFooter from "../../../components/CommonFooter"

const statusOptions = [
    { value: '', label: 'Trạng thái', number: 0 },
    { value: true, label: 'Hiển thị', number: 2 },
    { value: false, label: 'Ẩn đi', number: 3 }
]

const GalleryDetail = () => {
    const galleryId = useParams().id
    const [status, setStatus] = useState('')

    const dispatch = useDispatch()
    const store = useSelector(state => state.gallery)
    useEffect(() => {
        dispatch(getGallery(galleryId))
    }, [dispatch])

    const handleChange = (value) => {
        setStatus(value?.value)
    }

    const handleCheckValueInGallery = () => {
        // return true
        if (store?.gallery?.is_active) {
            return store?.gallery?.is_active
        } else return status
    }

    async function handleUploadStatus() {
        if (status) {
            store.gallery.is_active = status
            const res = await updateStatusGallery(store.gallery)
            if (res?.status) {
                store.gallery = null
                history?.go(-1)
            } else {
                //show error message
            }
        }
    }

    return (
        <>
            <Row>
                <Col md={6}>
                    <Card className={'w-100 h-100'}>
                        <div className={'gallery d-flex'}>
                            <div className={'gallery-img col-4'}>
                                <img src={store?.gallery?.image_url} alt="imgGallery"/>
                            </div>
                            <div className={'gallery-info col-8'}>
                                <div className={'gallery-info--name-customer'}>
                                    <p>Tên người gửi: <span className={'name'}>{store?.gallery?.name}abc</span></p>
                                    <p>Email: <span className={'name'}>{store?.gallery?.email}abc</span></p>
                                    <p>Message: <span className={'name'}>{store?.gallery?.message}test</span></p>
                                </div>
                                <div className={'gallery-info--status d-flex align-items-center justify-content-between'}>
                                    <p className={'m-0'}>Trạng thái: </p>
                                    <CreatableSelect
                                        styles={{cursor: 'pointer'}}
                                        className={'w-50'}
                                        placeholder={'Trạng thái'}
                                        isClearable
                                        defaultInputValue={handleCheckValueInGallery}
                                        onChange={newValue => {
                                            console.log(newValue)
                                            handleChange(newValue)
                                        }
                                        }
                                        options={statusOptions}
                                    />
                                </div>
                            </div>
                        </div>
                    </Card>
                </Col>
            </Row>
            <CommonFooter handleSubmitButton={handleUploadStatus}/>
        </>
    )
}

export default GalleryDetail
